        "Améliorer la connaissance et la compréhension du fonctionnement de l'écosystème de la route au sein de votre organisation, passe par votre capacité à intégrer de nouveaux collaborateurs, conscients et avertis prêts à faire face à toutes les situations de mobilité, et engagés à améliorer leur comportement sur la route. Par défaut, toutes les personnes qui passent le test E.D.T sont soumises au même format de test 45 minutes durant lesquelles il faut répondre à 200 questions.",
        "Améliorer la connaissance et la compréhension du fonctionnement de l'écosystème de la route au sein de votre organisation, passe par votre capacité à intégrer de nouveaux collaborateurs, conscients et avertis prêts à faire face à toutes les situations de mobilité, et engagés à améliorer leur comportement sur la route. Par défaut, toutes les personnes qui passent le test E.D.T sont soumises au même format de test 45 minutes durant lesquelles il faut répondre à 200 questions.",
        "Améliorer la connaissance et la compréhension du fonctionnement de l'écosystème de la route au sein de votre organisation, passe par votre capacité à intégrer de nouveaux collaborateurs, conscients et avertis prêts à faire face à toutes les situations de mobilité, et engagés à améliorer leur comportement sur la route. Par défaut, toutes les personnes qui passent le test E.D.T sont soumises au même format de test 45 minutes durant lesquelles il faut répondre à 200 questions."
