const diagColors = [
    { r: '255', g: '84', b: '56' },
    { r: '255', g: '131', b: '0' },
    { r: '237', g: '197', b: '11' },
    { r: '143', g: '204', b: '41' },
    { r: '41', g: '204', b: '97' }
];

function toRgba({ r, g, b }, opacity) {
    if (!opacity) {
        opacity = 1;
    }

    return `rgba(${r}, ${g}, ${b}, ${opacity})`;
}

function getDiagColors(successRate) {
    const color = diagColors[Math.trunc(successRate / 20)];

    return {
        backgroundColor: toRgba(color, 0.5),
        borderColor: toRgba(color)
    };
}

function downloadCertificate() {
    html2canvas(document.querySelector('.content_result')).then(function(
        canvas
    ) {
        const downloadLink = document.getElementById('download_certificate');
        downloadLink.href = canvas.toDataURL('image/png');
        downloadLink.download = 'easy-driver-certificate.png';
        downloadLink.click();
    });
}

function countQuestionTypeFor(type) {
    const ctx = window.testContext;
    const { questions, generatedCorpus } = ctx;

    const selectedQuestions = questions.filter(q =>
        generatedCorpus.includes(q.id)
    );

    return selectedQuestions.filter(q => q.categorie === type).length;
}

function roundOneDec(numb) {
    return Math.round(numb * 10) / 10;
}

function computeResult() {
    const ctx = window.testContext;
    const { questions, generatedCorpus, userAnswers } = ctx;

    const rightAnswersCount = {
        comprehension: 0,
        knowledge: 0,
        behavior: 0,
        culture: 0
    };

    for (let cnt = 0; cnt < userAnswers.length; cnt++) {
        let userAnswer = userAnswers[cnt];

        if (userAnswer.length === 0) {
            continue;
        }

        const question = questions.find(q => q.id === generatedCorpus[cnt]);

        if (question.gameplay === 'txt_tr') {
            userAnswer = userAnswer.map((a, index) =>
                (a === question.reponses[index].correct).toString()
            );
        } else if (question.gameplay === 'curseur') {
            userAnswer = userAnswer.map(a =>
                (parseInt(a) === question.reponses.true).toString()
            );
        }

        const extractedValues = userAnswer.map(
            a => (typeof a !== 'string' ? a.value : a)
        );
        const isCorrect = !extractedValues.some(a => a === 'false');

        if (isCorrect) {
            switch (question.categorie) {
                case 'compréhension':
                    rightAnswersCount.comprehension += 1;
                    break;
                case 'connaissance':
                    rightAnswersCount.knowledge += 1;
                    break;
                case 'comportement':
                    rightAnswersCount.behavior += 1;
                    break;
                case 'culture':
                    rightAnswersCount.culture += 1;
            }
        }
    }

    const { comprehension, knowledge, behavior, culture } = rightAnswersCount;

    return {
        overallSuccessRate:
            roundOneDec(
                (comprehension + knowledge + behavior) / generatedCorpus.length
            ) * 100,
        comprehensionSuccessRate:
            roundOneDec(comprehension / countQuestionTypeFor('compréhension')) *
            100,
        knowledgeSuccessRate:
            roundOneDec(knowledge / countQuestionTypeFor('connaissance')) * 100,
        behaviorSuccessRate:
            roundOneDec(behavior / countQuestionTypeFor('comportement')) * 100,
        cultureSuccessRate:
            roundOneDec(culture / countQuestionTypeFor('culture')) * 100
    };
}

function loadResult(pageResult) {
    const {
        overallSuccessRate,
        comprehensionSuccessRate,
        knowledgeSuccessRate,
        behaviorSuccessRate,
        cultureSuccessRate
    } = computeResult();

    clearInterval(timer);
    removeAllChild(pageResult);

    const titleElem = document.createElement('h1');
    titleElem.innerHTML = 'Résultats';
    const headerElem = document.createElement('div');
    headerElem.className = 'result_header';
    headerElem.appendChild(titleElem);

    const percentageElem = document.createElement('h1');
    percentageElem.innerHTML = overallSuccessRate + '%';
    percentageElem.style.color = toRgba(diagColors[overallSuccessRate / 20]);
    const percentageTextElem = document.createElement('h2');
    percentageTextElem.innerHTML = 'de bonnes réponses';
    const percentageContainerElem = document.createElement('div');
    percentageContainerElem.className = 'result_percentage-container';
    percentageContainerElem.appendChild(percentageElem);
    percentageContainerElem.appendChild(percentageTextElem);

    const canvasElem = document.createElement('canvas');
    const canvasContainerElem = document.createElement('div');
    canvasContainerElem.className = 'result_diag';
    canvasContainerElem.appendChild(canvasElem);

    const bodyElem = document.createElement('div');
    bodyElem.className = 'result_body';
    bodyElem.appendChild(percentageContainerElem);
    bodyElem.appendChild(canvasContainerElem);

    const ctx = canvasElem.getContext('2d');
    const chart = new Chart(ctx, {
        type: 'radar',
        label: '',
        data: {
            labels: [
                'Connaissances: ' + knowledgeSuccessRate + '%',
                'Compréhension: ' + comprehensionSuccessRate + '%',
                'Comportement: ' + behaviorSuccessRate + '%',
                'Culture: ' + cultureSuccessRate + '%'
            ],
            datasets: [
                Object.assign(
                    {},
                    {
                        data: [
                            knowledgeSuccessRate,
                            comprehensionSuccessRate,
                            behaviorSuccessRate,
                            cultureSuccessRate
                        ],
                        pointRadius: 0
                    },
                    getDiagColors(overallSuccessRate)
                )
            ]
        },
        options: {
            scale: {
                ticks: {
                    min: 0,
                    max: 100,
                    maxTicksLimit: 5,
                    display: false
                },
                angleLines: {
                    color: 'black'
                },
                pointLabels: {
                    fontSize: 16,
                    fontColor: 'black'
                }
            },
            legend: {
                display: false
            },
            gridLines: {
                color: ''
            }
        }
    });

    const successTextElem = document.createElement('p');
    successTextElem.innerHTML = getSuccessText(overallSuccessRate);
    const backToLandingButtonElem = document.createElement('button');
    backToLandingButtonElem.innerHTML = 'retour au site';
    const footerElem = document.createElement('div');
    footerElem.className = 'result_footer';
    footerElem.appendChild(successTextElem);
    footerElem.appendChild(backToLandingButtonElem);

    pageResult.className = 'result_page';
    pageResult.appendChild(headerElem);
    pageResult.appendChild(bodyElem);
    pageResult.appendChild(footerElem);
} //end loadResult

function removeAllChild(parentBox) {
    //empty content_box_quiz (tantque a enfant => suppr)
    while (parentBox.hasChildNodes()) {
        parentBox.removeChild(parentBox.firstChild);
    }
}

export { loadResult };
